html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.minefield {
  display: grid;
  grid-template-rows: repeat(9, 20px);
  grid-template-columns: repeat(9, 20px);
  width: 180px;
  height: 180px;
  padding: 7px;
  border: 2px solid #707070;
  background: #bdbdbd;
  border-radius: 3px;
}

.cell {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  box-sizing: border-box;
  border-bottom: solid 2px #7b7b7b;
  border-right: solid 2px #7b7b7b;
  border-top: solid 2px #fff;
  border-left: solid 2px #fff;
}

.empty-cell {
  width: 100%;
  height: 100%;
  border: solid 1px #7b7b7b;
}

.count-cell {
  font-size: 20px;
}

.flag-cell {
  width: 100%;
  height: 100%;
  background-image: url(/silly-minesweeper-online/static/media/flag.2739582e.svg);
}

.mine-cell {
  width: 100%;
  height: 100%;
  background-image: url(/silly-minesweeper-online/static/media/mine.7b109137.svg);
}

.smile, .smile-lose, .smile-win {
  width: 40px;
  height: 40px;
  margin-bottom: 20px;
  background-image: url(data:image/png;base64,);
  -webkit-transition: 1s, margin-bottom 0s;
  transition: 1s, margin-bottom 0s;
}

.smile, .smile-lose, .smile-win, .glasses-smile-win, .blunt-smile-win {
  background-repeat:no-repeat;
  background-size:contain;
  background-position:center;
}

.smile-lose {
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}

.smile-win, .glasses-smile-win {
  position: absolute;
  width: 500px;
  height: 500px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}

.glasses {
  position: absolute;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  -webkit-transition: 2s;
  transition: 2s;
  background-image: url(data:image/png;base64,);
}

.blunt {
  position: absolute;
  -webkit-transform: translate(-300%, 20%);
          transform: translate(-300%, 20%);
  -webkit-transition: 2s;
  transition: 2s;
  background-image: url(/silly-minesweeper-online/static/media/thuglife.3a1a6dc1.png);
}

.glasses-smile-win {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  -webkit-transform: translateY(-20%);
          transform: translateY(-20%);
  z-index: 2;
}

.blunt-smile-win {
  width: 300px;
  height: 300px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  -webkit-transform: translate(-50%, 50%);
          transform: translate(-50%, 50%);
  z-index: 2;
}
.App {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

